<template>
  <div
    class="activity-theme-container"
    :class="{'theme-activated': isChoosen}"
    @click="toggleActivity"
  >
    <p>{{theme}}</p>
  </div>
</template>

<script>
export default {
  name: "ActivityTheme",
  props: {
    theme: {
      type: String,
      default: () => "Theme"
    }
  },
  computed: {
    choosenTheme() {
      return this.$store.state.theme;
    },
    isChoosen() {
      return this.choosenTheme == this.theme;
    }
  },
  methods: {
    toggleActivity() {
      this.$store.commit("toggleTheme", this.theme);
    }
  }
};
</script>

<style>
.activity-theme-container {
  align-self: center;
  background-color: transparent;
  border-radius: 25px;
  box-shadow: 4px 4px 9px #d5d5d5, 
             -4px -4px 9px #ffffff;;
  color: grey;
  display: flex;
  flex-flow: column nowrap;
  padding: 4px 32px;
  transition: 300ms all ease-out;
}

.activity-theme-container:hover {
  color: white;
  background-color: orange;
}

.theme-activated {
  color: white;
  background-color: orange;
}

@media only screen and (max-width: 576px) {
  .activity-theme-container {
    align-self: stretch;
    text-align: center;
    justify-content: center;
    align-items: center;
  }
}
</style>