<template>
  <div v-if="menuToggled" id="about">
    <div class="theme-container">
      <ActivityTheme class="activity-theme" v-for="theme in themes" :key="theme" :theme="theme"></ActivityTheme>
    </div>
    <ActivityCollection :items="themeActivities" :title="choosenTheme"></ActivityCollection>
  </div>
</template>

<script>
import ActivityCollection from "../components/activity/ActivityCollection";
import ActivityTheme from "../components/activity/ActivityTheme";
import ActivityService from "../services/activity-service";

export default {
  name: "ITalent",
  components: {
    ActivityCollection,
    ActivityTheme
  },
  computed: {
    menuToggled() {
      if (this.widthBrowser < 972) {
        return this.$store.state.isMenuToggled;
      } else {
        return true;
      }
    },
    widthBrowser() {
      return Math.max(
        document.documentElement.clientWidth,
        window.innerWidth || 0
      );
    },
    choosenTheme() {
      return this.$store.state.theme;
    },
    themeActivities() {
      return ActivityService.getActivityByCategory(this.choosenTheme);
    }
  },
  data() {
    return {
      themes: ActivityService.getCategories()
    };
  }
};
</script>

<style>
#about {
  display: flex;
  flex-flow: column nowrap;
}

.activity-theme {
  margin: 16px 16px;
}

.theme-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

@media only screen and (max-width: 768px) {
  .theme-container {
    flex-flow: column nowrap;
  }
}
</style>
