<template>
  <div v-if="menuToggled" id="home">
    <div class="home-section-one">
      <div class="home-typewriter">
        <TypeWriter :name="name"></TypeWriter>
      </div>
      <div class="home-quote">
        <Quote></Quote>
      </div>
      <div class="home-download">
        <CustomButton title="Download mij" />
      </div>
    </div>
    <hr class="section-seperator" />
    <h1 class="section-title">Introduction</h1>
    <div class="home-section-two">
      <div class="home-section-two-sidebar"></div>
      <div class="home-section-two-content">
        <div v-for="(paragraph, index) in introduction" :key="index">
          <p :class="{'summary' : index == 0}">{{paragraph}}</p>
          <br v-if="index != 4" />
        </div>
      </div>
      <div class="home-section-two-sidebar"></div>
    </div>
  </div>
</template>

<script>
import TypeWriter from "./../components/home/TypeWriter";
import Quote from "./../components/home/Quote";
import CustomButton from "../components/button/CustomButton";
import IntroductionService from "../services/introduction-service";

export default {
  components: { TypeWriter, Quote, CustomButton },
  name: "Home",
  data() {
    return {
      name: "Aäron Noels,",
      introduction: IntroductionService.getFullIntroduction()
    };
  },
  computed: {
    menuToggled() {
      if (this.widthBrowser < 972) {
        return this.$store.state.isMenuToggled;
      } else {
        return true;
      }
    },
    widthBrowser() {
      return Math.max(
        document.documentElement.clientWidth,
        window.innerWidth || 0
      );
    }
  }
};
</script>

<style>
.home {
  display: flex;
  flex: 1 1 auto;
}
.home-section-one {
  display: flex;
  flex-flow: column nowrap;
}

.home-section-two {
  display: flex;
  flex-flow: row nowrap;
}

.home-section-two-content {
  border-radius: 25px;
  box-shadow: 20px 20px 60px #d9d9d9, 
             -20px -20px 60px #ffffff;
  display: flex;
  flex-flow: column nowrap;
  flex: 6;
  padding: 32px;
}

.home-section-two-sidebar {
  flex: 3;
}

.home-quote {
  display: flex;
  text-align: center;
}

.home-typewriter {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.home-download {
  display: flex;
  justify-content: center;
}

.section-seperator {
  border-top: 5px solid orange;
  width: 80%;
}

.section-title {
  color: orange;
  font-weight: bold;
  margin: 32px 0px;
  text-align: center;
  width: 100%;
}

.summary {
  color: black;
  font-weight: bold;
}

@media only screen and (max-width: 972px) {
  .home-section-two-content {
    flex: 8;
  }

  .home-section-two-sidebar {
    flex: 2;
  }
}

@media only screen and (max-width: 576px) {
  .home-section-two-content {
    flex: 10;
  }

  .home-section-two-sidebar {
    flex: 1;
  }
}
</style>
