<template>
  <div v-if="menuToggled" class="footer-container">
    <div class="footer-content">
      <FooterItem
        imageClass="fab fa-github"
        link="https://github.com/aaronnoels2010"
        name="Github"
      />
      <FooterItem
        imageClass="fab fa-linkedin-in"
        link="https://be.linkedin.com/in/a%C3%A4ron-noels-70b94b173/nl"
        name="LinkedIn"
      />
      <FooterItem
        imageClass="fab fa-facebook-f"
        link="https://www.facebook.com/aaron.noels"
        name="Facebook"
      />
    </div>
    <div class="footer-logo">
      <PxlLogo />
    </div>
  </div>
</template>

<script>
import FooterItem from "./FooterItem";
import PxlLogo from "./PxlLogo";
export default {
  name: "Footer",
  components: {
    FooterItem,
    PxlLogo
  },
  computed: {
    menuToggled() {
      if (this.widthBrowser < 972) {
        return this.$store.state.isMenuToggled;
      } else {
        return true;
      }
    },
    widthBrowser() {
      return Math.max(
        document.documentElement.clientWidth,
        window.innerWidth || 0
      );
    }
  }
};
</script>

<style>
.footer-container {
  justify-self: flex-start;
  display: flex;
  flex-flow: row nowrap;
  flex: 0 0 0;

  margin: 16px;
}

.footer-content {
  display: flex;
  flex: 13;
  flex-direction: row nowrap;
  justify-content: space-evenly;
  align-items: center;
}

.footer-logo {
  display: flex;
  flex-flow: column nowrap;
  height: 100px;
  padding-right: 16px;
}

@media only screen and (max-width: 576px) {
  .footer-container {
    flex-flow: column nowrap;
    align-items: center;
  }

  .footer-content {
    flex-flow: column nowrap;
  }

  .footer-logo {
    padding: 0px;
    width: 100px;
  }
}
</style>