<template>
  <div @click="toggleMenu" id="nav-item-container">
    <router-link :to="`/${name}`" class="nav-item-link">{{ name }}</router-link>
  </div>
</template>

<script>
export default {
  name: "NavItem",
  props: {
    name: String
  },
  methods: {
    toggleMenu() {
      this.$store.commit("toggleMenu");
    }
  },
  computed: {
    toggledMenu() {
      return this.$store.state.isMenuToggled;
    }
  }
};
</script>

<style>
#nav-item-container {
  display: flex;
  justify-content: center;
}

.nav-item-link {
  font-size: 24px;
  font-weight: bold;
  font-family: sans-serif;
  text-transform: uppercase;
  letter-spacing: 1;
}

.nav-item-link {
  color: black;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.15em;

  display: inline-block;
  padding: 15px 20px;
  position: relative;
}

.nav-item-link:hover {
  text-decoration: none;
  color: black;
}

.nav-item-link:after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 4px;
  left: 50%;
  position: absolute;
  background: orange;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}
.nav-item-link:hover:after {
  width: 100%;
  left: 0;
}

.router-link-active {
  color: orange;
}

@media only screen and (max-width: 972px) {
  #nav-item-container {
    justify-content: center;
  }
}
</style>
