import { ACTIVITY_DATA, CATEGORY_DATA } from "../assets/data/activity";

export default class ActivityService {
  static getActivityByCategory(category) {
    return ACTIVITY_DATA.filter(activity => activity.category == category);
  }

  static getActivityById(id) {
    return ACTIVITY_DATA.find(activity => activity.id == id);
  }

  static getCategories() {
    return [...CATEGORY_DATA];
  }
}
