<template>
  <router-link :to="`/${activity.category}/${activity.id}`">
    <div class="activity-preview">
      <div class="activity-card-image">
        <img :src="activity.image" class="activity-preview-image" />
      </div>
      <div class="activity-card-content">
        <span class="activity-content-title">{{checkLength(activity.title, 34).toUpperCase()}}</span>
        <span class="activity-content-content">{{checkLength(activity.content[0], 170)}}</span>
      </div>
    </div>
  </router-link>
</template>

<script>
export default {
  name: "ActivityPreview",
  props: {
    activity: {
      type: Object,
      default: () => {}
    }
  },
  methods: {
    checkLength(string, size) {
      if (string.length > size) {
        return `${string.substring(0, size - 4)} ...`;
      } else {
        return string;
      }
    }
  }
};
</script>

<style>
.activity-preview {
  background: lightgray;
  border-radius: 25px;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  height: 425px;
  width: 250px;
  overflow: hidden;
  margin: 16px;
}

.activity-card-image {
  background-color: transparent;
  display: flex;
  flex-direction: column nowrap;
  flex: 1 0 auto;
  border-radius: 25px;
  margin: 10px;
  overflow: hidden;
}

.activity-preview-image {
  height: 100%;
  width: 100%;
  transition: 1000ms ease transform;
}

.activity-preview-image:hover {
  transform: scale(1.5);
}

.activity-card-content {
  background-color: white;
  border-radius: 25px 25px 0px 0px;
  display: flex;
  flex: 2 0 auto;
  flex-flow: column nowrap;
  text-align: center;
}

.activity-content-title {
  font-size: 1.4em;
  font-weight: bolder;
  color: gray;
  padding: 0px 8px;
}

.activity-content-content {
  font-weight: bold;
  color: gray !important;
}

@media only screen and (min-width: 972px) {
  .activity-preview:hover {
    -webkit-animation: shadow-drop-2-center 0.4s
      cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    animation: shadow-drop-2-center 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94)
      both;
  }

  @-webkit-keyframes shadow-drop-2-center {
    0% {
      -webkit-transform: translateZ(0);
      transform: translateZ(0);
      -webkit-box-shadow: 0 0 0 0 black;
      box-shadow: 0 0 0 0 black;
    }
    100% {
      -webkit-transform: translateZ(50px);
      transform: translateZ(50px);
      -webkit-box-shadow: 0 0 20px 0px lightgray;
      box-shadow: 0 0 20px 0px lightgray;
    }
  }
  @keyframes shadow-drop-2-center {
    0% {
      -webkit-transform: translateZ(0);
      transform: translateZ(0);
      -webkit-box-shadow: 0 0 0 0 lightgray;
      box-shadow: 0 0 0 0 lightgray;
    }
    100% {
      -webkit-transform: translateZ(50px);
      transform: translateZ(50px);
      -webkit-box-shadow: 0 0 20px 0px lightgray;
      box-shadow: 0 0 20px 0px lightgray;
    }
  }
}
</style>