<template>
  <div class="activity-detail-container" v-if="menuToggled">
    <div class="activity-detail-sidebar"></div>
    <div class="activity-detail-main">
      <h1 class="activity-detail-main-title">{{activity.title}}</h1>
      <div class="activity-detail-image">
        <img class="activity-detail-image-image" :src="activity.image" />
      </div>
      <div class="activity-detail-info">
        <IconWithText :content="activity.company" icon="fas fa-building"></IconWithText>
        <IconWithText :content="activity.place" icon="fas fa-thumbtack"></IconWithText>
        <IconWithText
          :content="getDateString(activity.startDate) + ' - ' + getDateString(activity.endDate)"
          icon="fas fa-calendar-week"
        ></IconWithText>
      </div>
      <div class="activity-detail-content">
        <div v-for="(paragraph, index) in activity.content" :key="index">
          <p
            class="activity-detail-content-content"
            :class="{'introduction': index == 0}"
          >{{paragraph}}</p>
          <br v-if="index != lengthContent - 1" />
        </div>
      </div>
    </div>
    <div class="activity-detail-sidebar"></div>
  </div>
</template>

<script>
import ActivityService from "../services/activity-service";
import IconWithText from "../components/contact/IconWithText";
export default {
  name: "ActivityDetail",
  components: {
    IconWithText
  },
  computed: {
    lengthContent() {
      return this.activity.content.length;
    },
    menuToggled() {
      if (this.widthBrowser < 972) {
        return this.$store.state.isMenuToggled;
      } else {
        return true;
      }
    },
    widthBrowser() {
      return Math.max(
        document.documentElement.clientWidth,
        window.innerWidth || 0
      );
    }
  },
  data() {
    return {
      activity: ActivityService.getActivityById(this.$route.params.id)
    };
  },
  methods: {
    getDateString(date) {
      const options = {
        day: "numeric",
        month: "numeric",
        year: "numeric",
        hour: "numeric",
        minute: "numeric"
      };
      return date.toLocaleTimeString("nl-NL", options);
    }
  }
};
</script>

<style>
.activity-detail-container {
  display: flex;
  flex-flow: row nowrap;
}

.activity-detail-content {
  align-items: center;
  display: flex;
  flex-flow: column nowrap;
  border-radius: 15px 50px 30px;
  box-shadow: 0px 0px 7px lightgray;
  padding: 16px 32px;
}

.activity-detail-content-content {
  margin-bottom: 16px;
}

.activity-detail-image {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  margin: 16px 0px;
}

.activity-detail-image-image {
  border-radius: 10px;
  width: 70%;
}

.activity-detail-main {
  display: flex;
  flex-flow: column nowrap;
  flex: 6;
}

.activity-detail-main-title {
  color: gray;
  text-align: center;
}

.activity-detail-sidebar {
  display: flex;
  flex: 3;
}

.introduction {
  font-weight: bold;
}

@media only screen and (max-width: 972px) {
  .activity-detail-main {
    flex: 8;
  }

  .activity-detail-sidebar {
    flex: 2;
  }
}

@media only screen and (max-width: 576px) {
  .activity-detail-main {
    flex: 10;
  }

  .activity-detail-sidebar {
    flex: 1;
  }

  .activity-detail-content-content {
    border-radius: 30px;
  }
}
</style>