<template>
  <div class="information-container">
    <h1 class="information-title">{{ title }}</h1>
    <div class="information-content" :style="{'flex-flow': this.flexFlow}">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "Information",
  props: {
    title: {
      type: String,
      default: () => "title"
    },
    flexFlow: {
      type: String,
      default: () => "column nowrap"
    }
  }
};
</script>

<style>
.information-container {
  display: flex;
  flex-flow: column nowrap;
  margin-bottom: 16px;
}

.information-content {
  background-color: transparent;
  display: flex;
  justify-content: flex-start;
  padding: 8px;
}

.information-title {
  color: gray;
  font-size: 24px;
  text-align: center;
}
</style>
