<template>
  <div id="navbar-container">
    <button v-on:click="toggleMenu" id="navbar-menu">
      <i id="navbar-menu-icon" class="fas fa-bars"></i>
    </button>
    <div id="navbar-items">
      <NavItem name="home"></NavItem>
      <NavItem name="i-talent"></NavItem>
      <CircleAvatar />
      <NavItem name="projects"></NavItem>
      <NavItem name="about"></NavItem>
    </div>
  </div>
</template>

<script>
import CircleAvatar from "./CircleAvatar";
import NavItem from "./NavItem";
export default {
  name: "NavBar",
  components: {
    CircleAvatar,
    NavItem
  },
  methods: {
    toggleMenu: function() {
      this.$store.commit("toggleMenu");
    }
  },
  computed: {
    toggledMenu() {
      return this.$store.state.isMenuToggled;
    },
    widthBrowser() {
      return Math.max(
        document.documentElement.clientWidth,
        window.innerWidth || 0
      );
    }
  },
  watch: {
    toggledMenu: function(newValue) {
      const navbar = document.getElementById("navbar-items");
      const menuIcon = document.getElementById("navbar-menu-icon");
      if (this.widthBrowser < 972) {
        if (!newValue) {
          navbar.style.display = "flex";
          menuIcon.className = "fas fa-times";
        } else {
          navbar.style.display = "none";
          menuIcon.className = "fas fa-bars";
        }
      }
    }
  }
};
</script>

<style>
#navbar-menu {
  display: none;
}

#navbar-items {
  display: flex;
  flex-flow: row nowrap;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
}

#navbar-container {
  margin: 16px 0px;
  background-color: white;
  display: flex;
  flex-flow: row nowrap;
  justify-content: stretch;
  top: 0;
}

@media only screen and (max-width: 972px) {
  #navbar-items {
    display: none;
    flex-flow: column nowrap;
    flex: 1 1 auto;
    width: 100%;
  }

  #navbar-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 16px;
    height: 100%;
  }

  #navbar-menu {
    border: none;
    background-color: white;
    display: block;
    top: 16px;
    left: 16px;
  }
}
</style>
