<template>
  <div v-if="menuToggled" class="contact-container">
    <div class="contact-sidebar"></div>
    <div class="contact-content">
      <ProjectCollection></ProjectCollection>
    </div>
    <div class="contact-sidebar"></div>
  </div>
</template>

<script>
import ProjectCollection from "../components/project/ProjectCollection";
export default {
  name: "Project",
  components: {
    ProjectCollection
  },
  computed: {
    menuToggled() {
      if (this.widthBrowser < 972) {
        return this.$store.state.isMenuToggled;
      } else {
        return true;
      }
    },
    widthBrowser() {
      return Math.max(
        document.documentElement.clientWidth,
        window.innerWidth || 0
      );
    }
  }
};
</script>

<style>
.contact-container {
  display: flex;
}

.contact-content {
  flex: 6;
}

.contact-sidebar {
  flex: 3;
}

@media only screen and (max-width: 972px) {
  .contact-content {
    flex: 8;
  }

  .contact-sidebar {
    flex: 2;
  }
}

@media only screen and (max-width: 576px) {
  .contact-content {
    flex: 10;
  }

  .contact-sidebar {
    flex: 1;
  }
}
</style>