<template>
  <a :href="link">
    <div class="footer-item" @mouseenter="handleMouseEnter" @mouseleave="handleMouseLeave">
      <i class="footer-item-icon" :class="imageClass"></i>
      <p class="footer-item-text">{{name}}</p>
    </div>
  </a>
</template>

<script>
export default {
  name: "FooterItem",
  methods: {
    handleMouseEnter: function(event) {
      event.srcElement.className += " footer-item-animation";
    },
    handleMouseLeave: function(event) {
      event.srcElement.className = event.srcElement.className.substring(
        0,
        event.srcElement.className.indexOf(" footer-item-animation")
      );
    }
  },
  props: {
    imageClass: {
      type: String,
      default: () => "fas fa-camera"
    },
    link: {
      type: String,
      default: () => "https://www.hbvl.be/"
    },
    name: {
      type: String,
      default: () => "NAME"
    }
  }
};
</script>

<style>
.footer-item {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}

.footer-item-icon {
  color: orange;
}

.footer-item-text {
  color: grey !important;
  font-weight: 550;
  margin-bottom: none;
  margin-left: 8px;
}
@media only screen and (min-width: 576px) {
  .footer-item-animation {
    -webkit-animation: rotate-scale-up 0.5s
      cubic-bezier(0.455, 0.03, 0.515, 0.955) both;
    animation: rotate-scale-up 0.5s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;
  }

  @-webkit-keyframes rotate-scale-up {
    0% {
      -webkit-transform: scale(1) rotateZ(0);
      transform: scale(1) rotateZ(0);
    }
    50% {
      -webkit-transform: scale(2) rotateZ(180deg);
      transform: scale(2) rotateZ(180deg);
    }
    100% {
      -webkit-transform: scale(1) rotateZ(360deg);
      transform: scale(1) rotateZ(360deg);
    }
  }
  @keyframes rotate-scale-up {
    0% {
      -webkit-transform: scale(1) rotateZ(0);
      transform: scale(1) rotateZ(0);
    }
    50% {
      -webkit-transform: scale(2) rotateZ(180deg);
      transform: scale(2) rotateZ(180deg);
    }
    100% {
      -webkit-transform: scale(1) rotateZ(360deg);
      transform: scale(1) rotateZ(360deg);
    }
  }
}

@media only screen and (max-width: 576px) {
  .footer-item {
    margin: 16px 0px;
  }
}
</style>