<template>
  <div v-if="menuToggled" class="about">
    <div class="about-sidebar"></div>
    <div class="about-content">
      <General />
      <Skills />
      <SpareTime />
      <WorkExperience />
      <Education />
      <SoftSkills />
    </div>
    <div class="about-sidebar"></div>
  </div>
</template>

<script>
import Education from "../components/contact/Education";
import General from "../components/contact/General";
import Skills from "../components/contact/Skills";
import SoftSkills from "../components/contact/SoftSkills";
import SpareTime from "../components/contact/SpareTime";
import WorkExperience from "../components/contact/WorkExperience";
export default {
  name: "About",
  computed: {
    menuToggled() {
      if (this.widthBrowser < 972) {
        return this.$store.state.isMenuToggled;
      } else {
        return true;
      }
    },
    widthBrowser() {
      return Math.max(
        document.documentElement.clientWidth,
        window.innerWidth || 0
      );
    }
  },
  components: {
    Education,
    General,
    Skills,
    SoftSkills,
    SpareTime,
    WorkExperience
  }
};
</script>

<style>
.about {
  display: flex;
  flex-flow: row nowrap;
}

.about-content {
  flex: 4;
}

.about-sidebar {
  flex: 4;
}

@media only screen and (max-width: 972px) {
  .about-content {
    flex: 8;
  }

  .about-sidebar {
    flex: 2;
  }
}
</style>
