import Vue from "vue";
import VueRouter from "vue-router";
import About from "../views/About.vue";
import Project from "../views/Project.vue";
import Error from "../views/Error.vue";
import Home from "../views/Home.vue";
import ITalent from "../views/I-Talent.vue";
import ActivityDetail from "../views/ActivityDetail.vue";
import ProjectDetail from "../views/ProjectDetail.vue";

Vue.use(VueRouter);

const routes = [
  { path: "/", redirect: { name: "home" } },
  { path: "/404", name: "error404", component: Error },
  { path: "/about", name: "about", component: About },
  { path: "/home", name: "home", component: Home },
  { path: "/i-talent", name: "i-talent", component: ITalent },
  { path: "/projects", name: "project", component: Project },
  { path: "/projects/:id", name: "project-detail", component: ProjectDetail },
  {
    path: "/:category/:id",
    name: "activity-detail",
    component: ActivityDetail,
  },
  { path: "*", redirect: { name: "error404" } },
];

const router = new VueRouter({
  routes: routes,
});

export default router;
