<template>
  <div v-if="menuToggled" class="project-detail-container">
    <div class="project-detail-sidebar"></div>
    <div class="project-detail-content">
      <h1 class="project-detail-title">{{project.title}}</h1>
      <img
        :class="{'project-detail-image-vertical': project.vertical, 'project-detail-image-horizontal': !project.vertical}"
        :src="project.image"
      />
      <div class="project-detail-info">
        <IconWithText :content="project.company" icon="fas fa-building"></IconWithText>
        <IconWithText :content="project.technology" icon="fas fa-flask"></IconWithText>
      </div>
      <p class="project-detail-description">{{project.description}}</p>
    </div>
    <div class="project-detail-sidebar"></div>
  </div>
</template>

<script>
import ProjectsService from "../services/project-service";
import IconWithText from "../components/contact/IconWithText";
export default {
  name: "ProjectDetail",
  data() {
    return {
      project: ProjectsService.getProjectById(this.$route.params.id)
    };
  },
  components: {
    IconWithText
  },
  computed: {
    menuToggled() {
      if (this.widthBrowser < 972) {
        return this.$store.state.isMenuToggled;
      } else {
        return true;
      }
    },
    widthBrowser() {
      return Math.max(
        document.documentElement.clientWidth,
        window.innerWidth || 0
      );
    }
  }
};
</script>

<style>
.project-detail-container {
  display: flex;
  flex-flow: row nowrap;
}

.project-detail-content {
  display: flex;
  flex-flow: column nowrap;
  flex: 6;
  align-items: center;
}

.project-detail-description {
  color: gray;
  font-weight: 550;
  padding: 0px 8px 8px 8px;
  width: 100%;
}

.project-detail-info {
  align-self: stretch;
  display: flex;
  flex-flow: column nowrap;
  padding: 8px;
  margin-top: 16px;
}

.project-detail-image-vertical {
  border-radius: 10px;
  width: 300px;
  height: 500px;
  overflow: hidden;
}

.project-detail-image-horizontal {
  border-radius: 10px;
  width: 500px;
  height: 300px;
  overflow: hidden;
}

.project-detail-title {
  color: orange;
  text-align: center;
}

.project-detail-sidebar {
  display: flex;
  flex: 3;
}

@media only screen and (max-width: 576px) {
  .project-detail-image-horizontal {
    width: 300px;
    height: 200px;
  }
}
</style>