<template>
  <div class="chip-container" :style="{'background-color': this.color, color: this.textColor}">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "Chip",
  props: {
    color: {
      type: String,
      default: () => "orange"
    },
    textColor: {
      type: String,
      default: () => "white"
    }
  }
};
</script>

<style>
.chip-container {
  border-radius: 25px;
  color: white;
  align-self: center;
  padding: 0px 8px;
  margin-right: 16px;
  margin-top: 8px;
}
</style>