<template>
  <Information flexFlow="row wrap" title="Spare Time">
    <Chip v-for="spareTime in items" :key="spareTime" color="orange" textColor="white">{{spareTime}}</Chip>
  </Information>
</template>

<script>
import Information from "../contact/Information";
import Chip from "../contact/CustomChip";
import SpareTimeService from "../../services/spare-time-service";
export default {
  name: "SpareTime",
  data() {
    return {
      items: SpareTimeService.getSpareTimes()
    };
  },
  components: {
    Information,
    Chip
  }
};
</script>

