<template>
  <div class="project-collection-container">
    <ProjectFocused :project="items[0]" />
    <div class="project-collection-content">
      <ProjectPreview v-for="project in items.slice(1)" :key="project.id" :project="project" />
    </div>
  </div>
</template>

<script>
import ProjectFocused from "./ProjectFocused";
import ProjectPreview from "./ProjectPreview";
import ProjectsService from "../../services/project-service";
export default {
  name: "ProjectCollection",
  components: {
    ProjectFocused,
    ProjectPreview
  },
  data() {
    return {
      items: ProjectsService.getProjects()
    };
  }
};
</script>

<style>
.project-collection-container {
  display: flex;
  flex-flow: column nowrap;
}

.project-collection-content {
  display: flex;
  flex-flow: row wrap;
}

@media only screen and (max-width: 576px) {
  .project-collection-content {
    flex-flow: column nowrap;
  }
}
</style>