import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isMenuToggled: true,
    theme: "extra"
  },
  mutations: {
    toggleMenu(state) {
      state.isMenuToggled = !state.isMenuToggled;
    },
    toggleTheme(state, theme) {
      state.theme = theme;
    }
  },
  actions: {},
  modules: {}
});
